import React from "react";

import { Container, Row, Col } from "react-bootstrap";
import { Alert } from "react-bootstrap";

import * as AppDialogs from "./AppDialogs";
import * as AppBodies from "./AppBodies";

import { RecipeIndex } from "./db/RecipeDB";

import { Settings } from "./model/Settings";
import { Plan } from "./model/Plan";
import { Recipe, serializeRecipes, deserializeRecipes } from "./model/Recipe";

import { IconBar } from "./component/IconBar";
import { MenuBar } from "./component/MenuBar";

import { Beforeunload } from "react-beforeunload";
import { isMobileOnly } from "react-device-detect";

import "bootstrap/dist/css/bootstrap.min.css";

type AppState = {
  recipeIndex: RecipeIndex;
  settings: Settings;
  plan: Plan;
  showDialog?: Dialog;
  recipeToEdit?: Recipe;
  error: string | null;
  unsavedChanges: boolean;
  isInitialTour: boolean;
};

enum Dialog {
  LoadFile,
  SaveFile,
  RecipeEditor,
  SavePlanXLS,
  ShoppingList,
  PlanSettings
}

class App extends React.Component<{}, AppState> {
  constructor(props: {}) {
    super(props);

    let recipeIndex = new RecipeIndex();
    let planSettings = new Settings();
    let plan = Plan.new(planSettings.nrRecipes);

    // Hard binding is needed if `loadFile` do not use arrow notation
    // this.loadFile = this.loadFile.bind(this);
    this.state = {
      recipeIndex: recipeIndex,
      settings: planSettings,
      plan: plan,
      showDialog: undefined,
      recipeToEdit: undefined,
      error: null,
      unsavedChanges: false,
      isInitialTour: false
    };
  }

  public renderDialog = AppDialogs.renderDialog;
  public renderInitialBody = AppBodies.renderInitialBody;
  public renderDesktopBody = AppBodies.renderDesktopBody;

  addRecipes = (newRecipes: Recipe[], fromFile = false) => {
    this.setState((st) => {
      // You want to save only if it is not a first file load
      let needsSave =
        st.unsavedChanges || !(fromFile && st.recipeIndex.count() === 0);

      let ret = {};
      let newRecipeIndex = st.recipeIndex.addAll(newRecipes);

      if (newRecipeIndex !== st.recipeIndex) {
        ret = {
          recipeIndex: newRecipeIndex,
          unsavedChanges: needsSave,
        };
      }

      return ret;
    });
  };

  setIsInitialTour = (t: boolean) => {
    this.setState({ isInitialTour: t });
  }

  saveRecipe = (recipe: Recipe) => {
    this.addRecipes([recipe]);
    this.closeRecipeEditor();
  };

  onFileLoaded = (ev: ProgressEvent<FileReader>) => {
    let res = ev.target?.result;

    if (typeof res == "string") {
      try {
        this.addRecipes(deserializeRecipes(res), true);
      } catch (e) {
        this.setState({ error: e.message });
      }
    }
    this.closeDialog();
  };

  loadFile = (file: File) => {
    var reader = new FileReader();
    reader.onload = (ev) => this.onFileLoaded(ev);
    reader.readAsText(file, "UTF-8");
  };

  genRecipeFileContent = () => {
    return serializeRecipes(this.state.recipeIndex.all());
  };

  fileSaved = () => {
    this.setState({ unsavedChanges: false });
    this.closeDialog();
  };

  showDialog = (dialog: Dialog) => {
    return () => this.setState({ showDialog: dialog });
  };

  closeDialog = () => {
    this.setState({ showDialog: undefined });
  };

  closeRecipeEditor = () => {
    this.setState({ recipeToEdit: undefined, showDialog: undefined });
  };

  dismissError = () => {
    this.setState({ error: null });
  };

  updateSettings = (ps: Settings) => {
    this.setState((st) => {
      return {
        settings: ps,
        plan: st.plan.setNrBuckets(ps.nrRecipes)
      };
    });
  };

  updatePlan = (plan: Plan) => {
    this.setState({
      plan: plan
    });
  };

  editRecipe = (recipe: Recipe) => {
    this.setState({ showDialog: Dialog.RecipeEditor, recipeToEdit: recipe });
  };

  deleteRecipe = (recipe: Recipe) => {
    this.setState((st) => {
      let newRecipeIndex = st.recipeIndex.delete(recipe.getId());

      return {
        recipeIndex: newRecipeIndex,
        showDialog: undefined,
        recipeToEdit: undefined,
        unsavedChanges: true,
      };
    });
  };

  render(this: App) {

    return (
      <Container fluid className="d-flex flex-column vh-100">
        <Alert
          variant="danger"
          onClose={this.dismissError}
          show={!!this.state.error}
          dismissible>
          {this.state.error}
        </Alert>

        {this.state.unsavedChanges &&
          <Beforeunload onBeforeunload={() => "You'll lose your data!"} />
        }

        {this.renderDialog()}

        <Row>
          <Col>
            <MenuBar />
          </Col>
        </Row>

        {!isMobileOnly
          ?
          <Row className="h-100" style={{ minHeight: 0 }}>
            <Col xs="auto" className="pl-0 pr-2 pt-3">
              <IconBar
                onLoadFile={this.showDialog(Dialog.LoadFile)}
                onNewRecipe={this.showDialog(Dialog.RecipeEditor)}
                onSaveFile={this.showDialog(Dialog.SaveFile)}
                onEditSettings={this.showDialog(Dialog.PlanSettings)}
                unsavedChanges={this.state.unsavedChanges} />
            </Col>
            {this.state.recipeIndex.count() === 0 && this.renderInitialBody()}
            {this.state.recipeIndex.count() > 0 && !isMobileOnly && this.renderDesktopBody()}
          </Row>
          :
          <Row>
            <Col className="mt-3">
              <Alert key="recipe" variant="danger">
                Mobile is not supported as for now. Please try it on a little bit bigger device. Sorry for that.
              </Alert>
            </Col>
          </Row>
        }

      </Container >
    );
  }
}

export default App;
