import React, { FunctionComponent } from "react";

import { Container, Row, Col } from "react-bootstrap";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import { Upload, Download, PlusCircle, Gear } from 'react-bootstrap-icons';

type IconBarProps = {
    onLoadFile: () => void;
    onNewRecipe: () => void;
    onSaveFile: () => void;
    onEditSettings: () => void;
    unsavedChanges: boolean;
};

export const IconBar: FunctionComponent<IconBarProps> = ({
    onLoadFile,
    onNewRecipe,
    onSaveFile,
    onEditSettings,
    unsavedChanges
}) => {
    return (
        <Container className="pl-0 ml-2 mr-2 p-0">
            <Row className="mb-4">
                <Col className="pr-0">
                    <OverlayTrigger
                        key="upload"
                        placement="right"
                        overlay={
                            <Tooltip id="tooltip-upload">
                                Load recipes
                            </Tooltip>
                        }>
                        <Upload
                            className="my-first-step-a"
                            style={{ color: "#ffffff", cursor: "pointer" }}
                            onClick={onLoadFile} />
                    </OverlayTrigger>
                </Col>
            </Row>
            <Row className="mb-4">
                <Col className="pr-0">
                    <OverlayTrigger
                        key="download"
                        placement="right"
                        overlay={
                            <Tooltip id="tooltip-download">
                                Save recipes
                            </Tooltip>
                        }>
                        {unsavedChanges
                            ? <Download
                                className="text-warning"
                                style={{ cursor: "pointer" }}
                                onClick={onSaveFile} />
                            : <Download
                                style={{ color: "#ffffff40" }} />
                        }
                    </OverlayTrigger>
                </Col>
            </Row>
            <Row className="mb-4">
                <Col className="pr-0">
                    <OverlayTrigger
                        key="new"
                        placement="right"
                        overlay={
                            <Tooltip id="tooltip-new">
                                Add new recipe
                            </Tooltip>
                        }>
                        <PlusCircle
                            className="my-first-step-b"
                            style={{ color: "#ffffff", cursor: "pointer" }}
                            onClick={onNewRecipe} />
                    </OverlayTrigger>
                </Col>
            </Row>
            <Row className="mb-4">
                <Col className="pr-0">
                    <OverlayTrigger
                        key="new"
                        placement="right"
                        overlay={
                            <Tooltip id="tooltip-new">
                                Settings
                            </Tooltip>
                        }>

                        <Gear style={{
                            color: "#ffffff",
                            cursor: "pointer"
                        }} onClick={onEditSettings} />
                    </OverlayTrigger>
                </Col>
            </Row>
        </Container>
    );
};
