import React, { FunctionComponent } from 'react';

import { Form, FormControl, InputGroup } from 'react-bootstrap';
import { Popover, OverlayTrigger } from 'react-bootstrap';
import { Card, ListGroup } from 'react-bootstrap';
import { Container, Row, Col } from 'react-bootstrap';

import { Gear, Eye, Search } from 'react-bootstrap-icons';

import { RecipeQuickView } from './RecipeQuickView'

import { Recipe } from '../model/Recipe';
import { Plan } from '../model/Plan';
import { Scoring, RecipeScoring } from '../model/Scoring';
import { RecipeIndex } from '../db/RecipeDB';

import { useDrag } from 'react-dnd'

import _ from 'lodash';

type RecipeSelectorProps = {
    recipeIndex: RecipeIndex,
    scoring: Scoring,
    plan: Plan,
    onEdit: (recipe: Recipe) => void
}

type RecipeListItemProps = {
    recipe: Recipe,
    scoring?: RecipeScoring,
    selected: boolean,
    onEdit: (recipe: Recipe) => void
}

const RecipeListItem: FunctionComponent<RecipeListItemProps> = ({ recipe, selected, scoring, onEdit }) => {

    const [{ isDragging }, drag] = useDrag({
        item: { type: "recipe", id: recipe.getId() },
        collect: (monitor) => ({
            isDragging: !!monitor.isDragging()
        })
    })

    const c = new Intl.Collator();
    const labels = recipe.getLabels()
        .sort(c.compare)
        .map(n => <Col key={n} sm="auto" className="mealee-recipe-selector-label mt-1 ml-2">{n}</Col>)

    return (
        <div
            className="hover"
            style={{
                position: 'relative',
                width: '100%',
                height: '100%',
                backgroundColor: '#FFFFFF'
            }}>

            <Card
                className="mealee-recipe-selector-item"
                key={recipe.getId()}
                ref={drag}
                style={{
                    opacity: isDragging || selected ? 0.5 : 1,
                    cursor: 'move',
                }}>
                <Card.Body className="p-0">
                    <Container>
                        <Row>
                            <Col className="pl-0 ml-2">
                                {recipe.getTitle()}
                            </Col>
                        </Row>
                        <Row>
                            {labels}
                        </Row>
                    </Container>
                </Card.Body>
            </Card>

            <div
                className="hover-on"
                style={{
                    position: 'absolute',
                    top: '50%',
                    transform: 'translateY(-50%)',
                    right: 10,
                    height: 20,
                    width: 40,
                    zIndex: 1,
                    opacity: 0.5
                }}>

                <Gear height="18" onClick={() => onEdit(recipe)} style={{ cursor: 'pointer' }} />
                &nbsp;
                <OverlayTrigger
                    placement="auto"
                    overlay={
                        <Popover id="popover-basic">
                            <Popover.Title as="h4">{recipe.getTitle()}</Popover.Title>
                            <Popover.Content>
                                <RecipeQuickView recipe={recipe} scoring={scoring} />
                            </Popover.Content>
                        </Popover>}>
                    <Eye height="18" style={{ cursor: 'pointer' }} />
                </OverlayTrigger>

            </div>

        </div >
    )
}

export const RecipeSelector: FunctionComponent<RecipeSelectorProps> = ({ recipeIndex, scoring, plan, onEdit }) => {

    const [filter, setFilter] = React.useState("");

    const debouncedSetFilter = React.useCallback(
        _.debounce(setFilter, 150),
        [], // will be created only once initially
    );

    const refresh = (event: React.ChangeEvent<HTMLInputElement>) => {
        debouncedSetFilter(event.currentTarget.value);
    }

    const filteredRecipes = (filter.trim().length === 0) ?
        recipeIndex.all() : recipeIndex.search(filter).map(res => res.key);

    const selectedRecipeSet = new Set(plan.getAllRecipeIds())

    const listItems = _.sortBy(filteredRecipes, r => scoring.getSum(r.getId()) * -1)
        .map((recipe) =>
            <ListGroup.Item className="p-0" key={recipe.getId()}>
                <RecipeListItem
                    key={recipe.getId()}
                    recipe={recipe}
                    scoring={scoring.getScoring(recipe.getId())}
                    onEdit={onEdit}
                    selected={selectedRecipeSet.has(recipe.getId())} />
            </ListGroup.Item>
        );

    return (
        <Form className="pt-2 h-100 d-flex flex-column" onSubmit={(e) => e.preventDefault()}>
            <InputGroup className="mb-1">
                <InputGroup.Prepend>
                    <InputGroup.Text id="basic-addon1"><Search /></InputGroup.Text>
                </InputGroup.Prepend>
                <FormControl type="text" placeholder="Filter recipes" onChange={refresh} />
            </InputGroup>
            <Form.Group controlId="recipeGroup" className="flex-grow-1 overflow-auto mealee-recipe-selector-list">
                <ListGroup>{listItems}</ListGroup>
            </Form.Group>
        </Form>
    );
}
