export interface IngredientJSON {
  name: string;
  amount: number;
  unit: string;
}

export interface RecipeJSON {
  id: string;
  title: string;
  source?: string;
  comment?: string;
  serving?: number;
  ingredients: IngredientJSON[];
  labels?: string[];
}

interface RecipeFileJSON {
  version: number;
  recipes: RecipeJSON[];
}

/* IMMUTABLE */
export class Ingredient {
  private constructor(name: string, amount: number, unit: string) {
    this.name = name;
    this.amount = amount;
    this.unit = unit;
  }

  public getName(): string {
    return this.name;
  }

  public getAmount(): number {
    return this.amount;
  }

  public getUnit(): string {
    return this.unit;
  }

  public static fromJSON(o: IngredientJSON): Ingredient {
    if (!o.name) {
      throw new Error("Missing ingredient name");
    }

    if (!o.unit) {
      throw new Error("Missing ingredient unit");
    }

    if (o.amount === undefined) {
      throw new Error("Missing ingredient amount");
    }

    return new Ingredient(o.name, o.amount, o.unit);
  }

  public toJSON(): IngredientJSON {
    return {
      name: this.name,
      amount: this.amount,
      unit: this.unit,
    };
  }

  private name: string;
  private amount: number;
  private unit: string;
}

function uuidv4() {
  return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function (c) {
    var r = (Math.random() * 16) | 0,
      v = c === "x" ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}

/* IMMUTABLE */
export class Recipe {
  private constructor(id: string, title: string) {
    this.id = id;
    this.title = title;
    this.ingredients = [];
    this.labels = [];
    this.serving = 4;
  }

  public static error = new Recipe("error", "Recipe cannot be found")

  public getId(): string {
    return this.id;
  }

  public getTitle(): string {
    return this.title;
  }

  public getComment(): string | undefined {
    return this.comment;
  }

  public getSource(): string | undefined {
    return this.source;
  }

  public getServing(): number {
    return this.serving;
  }

  public getIngredients(): Ingredient[] {
    return this.ingredients;
  }

  public getLabels(): string[] {
    return this.labels;
  }

  public static fromJSON(o: RecipeJSON): Recipe {
    if (!o.id) {
      o.id = uuidv4();
    }

    if (!o.title) {
      throw new Error("Missing recipe title");
    }

    let r = new Recipe(o.id, o.title);

    r.comment = o.comment;
    r.source = o.source;
    r.serving = o.serving ?? 4;
    r.labels = o.labels ?? [];

    if (Array.isArray(o.ingredients)) {
      r.ingredients = o.ingredients.map((i) => Ingredient.fromJSON(i));
    }

    return r;
  }

  public toJSON(): RecipeJSON {
    return {
      id: this.id,
      title: this.title,
      comment: this.comment,
      source: this.source,
      serving: this.serving,
      ingredients: this.ingredients.map((i) => i.toJSON()),
      labels: this.labels
    };
  }

  private id: string;
  private title: string;
  private comment?: string;
  private source?: string;
  private serving: number;
  private ingredients: Ingredient[];
  private labels: string[];
}

export function serializeRecipes(recipes: Recipe[]) {
  let str = JSON.stringify({ version: 1, recipes: recipes });
  let bytes = new TextEncoder().encode(str);
  let blob = new Blob([bytes], {
    type: "application/json;charset=utf-8",
  });
  return blob;
}

export function deserializeRecipes(data: string) {
  let json = JSON.parse(data);

  if (Array.isArray(json)) {
    let v0: RecipeJSON[] = json;
    return v0.map(Recipe.fromJSON);
  }

  if (typeof json === "object") {
    let r: RecipeFileJSON = json;

    if (typeof r.version !== "undefined") {
      if (r.version !== 1) {
        throw new Error("Invalid recipe file version");
      }

      if (Array.isArray(r.recipes)) {
        return r.recipes.map(Recipe.fromJSON);
      }
    }
  }

  throw new Error("Invalid recipe file");
}
