import React, { FunctionComponent } from 'react';

import { Modal, Form, Button, Tabs, Tab, Container } from 'react-bootstrap';

import { Settings } from '../model/Settings';

type SettingsDialogProps = {
    settings: Settings,
    onClose: () => void,
    onUpdate: (planSettings: Settings) => void
}

export const SettingsDialog: FunctionComponent<SettingsDialogProps> = (props) => {

    const changeNrRecipes = (newNrRecipes: number) => {
        let newSettings = props.settings.clone();
        newSettings.nrRecipes = newNrRecipes;
        props.onUpdate(newSettings);
    }

    return (
        <Modal show={true} onHide={props.onClose}>
            <Modal.Header closeButton>
                <Modal.Title>Settings</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Tabs defaultActiveKey="general">
                    <Tab eventKey="general" title="General">
                        <Container className="p-3">
                            <Form onSubmit={(e) => e.preventDefault()}>
                                <Form.Group controlId="nrRecipes">
                                    <Form.Label>Number of recipes:</Form.Label>
                                    <Form.Control as="select" value={props.settings.nrRecipes}
                                        onChange={(e) => changeNrRecipes(parseInt(e.target.value))}>
                                        <option key="3">3</option>
                                        <option key="4">4</option>
                                        <option key="5">5</option>
                                        <option key="6">6</option>
                                        <option key="7">7</option>
                                    </Form.Control>
                                </Form.Group>
                            </Form>
                        </Container>
                    </Tab>
                    <Tab eventKey="scoring" title="Scoring">

                    </Tab>
                </Tabs>
            </Modal.Body>
            <Modal.Footer>
                <Button type="button" variant="secondary" onClick={props.onClose}>Close</Button>
            </Modal.Footer>
        </Modal>
    );
}
